.fundo{
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
    border-radius: 6px;
    background-color: #481563;
    color: aliceblue;
    padding: 1rem;
    text-align: center;
    width: 30rem;
    z-index: 10;
    position: fixed;
    top: 20vh;
    left: calc(50% - 15rem);
}

.fundo h3{
    color: darkgrey;
}

.dados{
    width: 80%;
    margin-bottom: 20px;
    border-radius: 10px;
    padding: 10px;
    font-family: coolvetica;
    font-size: large;
    background-color: #FCA9EA;
    color: aliceblue;
    text-decoration: none;
    border: none;
}